import Vue from 'vue'
import Vuex from 'vuex'
import {backgrounds} from "@/ext";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        text: '',
        font: 'fontNickainleyNormal',
        size: 15,
        isWireWhite: true,
        color: {lineColor: '#fffac5', glowColor: '#ffb700', name: 'Оранжевый', id: 0,},
        stand: {
            img: 'https://i.ibb.co/ZTKdyWq/cut-around.png',
            name: 'По контуру',
            id: 1,
        },
        background: 1,
        backgrounds: backgrounds,
    },
    mutations: {
        changeText(state, val) {
            state.text = val;
        },
        changeFont(state, val) {
            state.font = val;
        },
        changeSize(state, val) {
            state.size = val;
        },
        changeWire(state, val) {
            state.isWireWhite = val;
        },
        changeColor(state, val) {
            state.color = val;
        },
        changeStand(state, val) {
            state.stand = val;
        },
        changeBackground(state, val) {
            state.background = val;
        },
        pushBackground(state, val) {
            if (backgrounds.filter(e => e.id === val.id).length === 0) {
                state.backgrounds.push(val);
            }
        },
    },
    actions: {
        changeText({commit}, val) {
            commit('changeText', val);
        },
        changeFont({commit}, val) {
            commit('changeFont', val);
        },
        changeSize({commit}, val) {
            commit('changeSize', val);
        },
        changeWire({commit}, val) {
            commit('changeWire', val);
        },
        changeColor({commit}, val) {
            commit('changeColor', val);
        },
        changeStand({commit}, val) {
            commit('changeStand', val);
        },
        changeBackground({commit}, val) {
            commit('changeBackground', val);
        },
        pushBackground({commit}, val) {
            commit('pushBackground', val);
        },
    },
    getters: {
        text: state => state.text,
        font: state => state.font,
        size: state => state.size,
        background: state => state.background,
        backgrounds: state => state.backgrounds,
        color: state => state.color,
        isWireWhite: state => state.isWireWhite,
        stand: state => state.stand,
    },
})