<template>
  <div id="tab-size">
    <div id="tab-size-slider-wrapper">
      <div id="tab-size-slider-inner">
        <vue-slider
            ref="slider"
            class="tab-size-slider"
            v-model="value"
            v-bind="options"
        ></vue-slider>
      </div>
      <div id="tab-size-slider-inner-text">{{ value }} см</div>
    </div>
    <div class="tab-description">
      Размер букв, отражённый на предпоказе отличается от действительного
    </div>
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'
import {mapActions, mapGetters} from "vuex";

export default {
  name: "TabSize",
  components: {
    VueSlider
  },
  created() {
    this.value = this.size;
  },
  watch: {
    value(val) {
      this.changeSize(val);
    }
  },
  computed: {
    ...mapGetters([
      'size',
    ]),
  },
  methods: {
    ...mapActions([
      'changeSize'
    ]),
  },
  data: () => ({
    value: 15,
    options: {
      dotSize: 20,
      height: 4,
      tooltip: 'none',
      tooltipPlacement: 'bottom',
      dataValue: 'value',
      min: 8,
      max: 60,
      processStyle: {
        backgroundColor: '#8b00ff',
      },
      tooltipStyle: {
        backgroundColor: '#8b00ff',
        borderColor: '#8b00ff',
      },
    }
  }),
}
</script>

<style scoped>
#tab-size {

}
#tab-size-slider-wrapper {
  display: flex;
  flex-direction: row;
}
#tab-size-slider-inner {
  display: flex;
  flex-grow: 1;
  align-items: center;
}
#tab-size-slider-inner-text {
  flex-shrink: 0;
  width: 55px;
  text-align: right;
}
#tab-size-slider-wrapper h1{
  text-align: center;
  margin-bottom: 0;
}
.tab-size-slider {
  margin: 0 10px;
  flex-grow: 1;
}
</style>