<template>
  <transition name="fade">
    <div id="modal-window" v-show="isModalOpen">
    <div id="modal-window-close" @click="closeAction"></div>
    <div id="modal-window-inner">
      <div
          id="modal-window-inner-header"
      >
        Оформление заказа
        <div
            id="modal-window-inner-header-close"
            @click="closeAction"
        >
          <svg fill="#ffffff" xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 24 24" width="48px" height="48px"><path d="M 4.7070312 3.2929688 L 3.2929688 4.7070312 L 10.585938 12 L 3.2929688 19.292969 L 4.7070312 20.707031 L 12 13.414062 L 19.292969 20.707031 L 20.707031 19.292969 L 13.414062 12 L 20.707031 4.7070312 L 19.292969 3.2929688 L 12 10.585938 L 4.7070312 3.2929688 z"/></svg>
        </div>
      </div>
      <form
          v-on:submit.prevent="onSubmit"
          action="https://forms.tildacdn.com/procces/"
          data-formactiontype="2"
          method="POST"
          id="modal-form"
          data-success-url="https://king-neon.ru/orderaccepted"
          data-success-callback="t678_onSuccess"
          role="form"
      >
        <div id="modal-form-inner">
          <input type="hidden" name="Phone" :value="phoneData ? phoneData.formatNational : ''">
          <input type="hidden" name="tildaspec-formname" tabindex="-1" value="Заказ из конструктора">
          <div class="modal-form-inner-col">
            <div class="modal-form-inner-title">Ваше имя</div>
            <input
                type="text"
                name="Name"
                class="modal-form-inner-input"
                placeholder="Кинг Неон"
                v-model="name"
                required
            >
            <div class="modal-form-inner-title">Телефон</div>
            <VuePhoneNumberInput
                name="tildaspec-phone-part[]"
                v-model="phone"
                @update="zhopa"
                default-country-code="RU"
                :border-radius="10"
                :translations="translations"
                size="lg"
                id="modal-form-inner-phone-input"
                color="#8b00ff"
                required
            />
            <div class="modal-form-inner-title">Ваш E-mail</div>
            <input
                type="text"
                name="Email"
                class="modal-form-inner-input"
                placeholder="king@neon.ru"
                v-model="email"
            >
            <div class="modal-form-inner-title">Способ обратной связи</div>
            <div class="modal-form-inner-feedback">
              <div class="modal-form-inner-feedback-item" v-for="(e, i) in feedbackVariants" :class="{'modal-form-inner-feedback-item__active': e.value === feedback,}" :key="e.value">
                <label
                    :for="e.id"
                    :style="{borderRight: `${(i !== feedbackVariants.length - 1) ? '1px solid #8b00ff' : 'none'}`,}"
                >
                  <input
                    type="radio"
                    :id="e.id"
                    name="Как Вам удобно было бы получить обратную связь"
                    :value="e.value"
                    v-model="feedback"
                  >
                  {{e.value}}
                </label>
              </div>
            </div>
          </div>
          <div class="modal-form-inner-col">
            <div class="modal-form-inner-title">Описание вывески</div>
            <textarea
                rows="4"
                placeholder="Описание вывески"
                class="modal-form-inner-input"
            ></textarea>
            <div class="modal-form-inner-title">Параметры заказа</div>
            <div class="modal-form-inner-constructor-data-items">
              <input type="hidden" name="Параметры заказа" :value="orderParamsString">
              <div class="modal-form-inner-constructor-data-item">Текст вывески: {{text}}</div>
              <div class="modal-form-inner-constructor-data-item">Шрифт: {{font.substr(4)}}</div>
              <div class="modal-form-inner-constructor-data-item">Цвет неона: {{color.name}}</div>
              <div class="modal-form-inner-constructor-data-item">Высота букв: {{size}} см</div>
              <div class="modal-form-inner-constructor-data-item">Подложка: {{stand.name}}</div>
              <div class="modal-form-inner-constructor-data-item">Кабель питания: {{isWireWhite ? 'Белый' : 'Чёрный'}}</div>
            </div>
            <div class="spacer"></div>
            <div id="modal-form-submit-wrapper">
              <button type="submit" id="modal-form-submit-btn">Заказать</button>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
  </transition>
</template>

<script>
import {mapGetters} from "vuex";
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';
import {formId, formWrapperId} from "@/ext";

export default {
  name: "FormOrder",
  computed: {
    ...mapGetters([
      'text',
      'font',
      'size',
      'isWireWhite',
      'stand',
      'color',
      'background',
      'backgrounds',
    ]),
    orderParamsString() {
      const params = {};

      params['text'] = `Текст вывески: ${this.text}`;
      params['font'] = `Шрифт: ${this.font.substr(4)}`;
      params['color'] = `Цвет неона: ${this.color.name}`;
      params['size'] = `Высота букв: ${this.size} см`;
      params['stand'] = `Подложка: ${this.stand.name}`;
      params['isWireWhite'] = `Кабель питания: ${this.isWireWhite ? 'Белый' : 'Чёрный'}`;

      return Object.values(params).reduce((result, curVal) => `${result}\n${curVal}`);
    },
  },
  watch: {
    name(val) {

    },
  },
  data: () => ({
    name: null,
    phone: null,
    email: null,
    feedbackVariants: [
      {value: 'Telegram', id: 'feedback-telegram', index: 0,},
      {value: 'WhatsApp', id: 'feedback-whatsapp', index: 1,},
      {value: 'Телефон', id: 'feedback-phone', index: 2,},
    ],
    phoneData: null,
    feedback: null,
    translations: {
      countrySelectorLabel: 'Код страны',
      countrySelectorError: 'Неверный код',
      phoneNumberLabel: 'Номер телефона',
      example: 'Пример :'
    },
  }),
  props: {
    isModalOpen: {
      type: Boolean,
      required: true,
    },
    closeAction: {
      type: Function,
      required: true,
    },
  },
  components: {
    VuePhoneNumberInput
  },
  methods: {
    zhopa(val) {
      this.phoneData = val;
    },
    onSubmit(e) {
      const ourFormClone = document.getElementById('modal-form-inner').cloneNode(true);
      const tildaForm = document.getElementById(formId);

      tildaForm.prepend(ourFormClone);
      tildaForm.submit();
    },
  },
}
</script>

<style scoped>
.modal-form-inner-feedback {
  display: flex;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  border: 1px solid #8b00ff;
  align-items: stretch;
  height: 48px;
}
.modal-form-inner-feedback-item {
  width: calc(100% / 3);
  box-sizing: border-box;
  z-index: 2;
  display: flex;
  align-items: stretch;
  transition: .3s;
}
.modal-form-inner-feedback-item__active {
  background-color: #8b00ff;
}
.modal-form-inner-feedback-item input {
  display: none;
}
.modal-form-inner-feedback-item label {
  flex-grow: 1;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}
.spacer {
  flex-grow: 1;
}
#modal-form-submit-wrapper {
  margin-top: 28px;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
#modal-form-submit-btn {
  outline: none;
  border: 2px solid #8b00ff;
  background-color: transparent;
  color: white;
  font-size: 20px;
  height: 56px;
  padding: 0 30px;
  font-family: 'Geometria', sans-serif;
}
#modal-form-inner-phone-input {
  margin-bottom: 28px;
}
.fade-leave-to, .fade-enter{
  transform: translateY(+1000px);
}
.fade-leave, .fade-enter-to {
  transform: translateY(0px);
}
.fade-enter-active, .fade-leave-active {
  transition: .6s !important;
}
.modal-form-inner-title {
  font-size: 20px;
  font-weight: 900;
  margin-bottom: 14px;
}
.modal-form-inner-input {
  box-sizing: border-box;
  margin-bottom: 28px;
  width: 100%;
  border: none;
  resize: none;
  outline: none;
  background-color: white;
  font-family: 'Geometria', sans-serif;
  padding: 14px;
  font-size: 16px;
  border-radius: 10px;
}
.modal-form-inner-constructor-data-items {
  display: flex;
  flex-wrap: wrap;
}
.modal-form-inner-constructor-data-item {
  margin: 4px 8px 4px 0;
  font-size: 14px;
  box-sizing: border-box;
  padding: 6px 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  background-color: rgba(255,255,255,0.1);
  text-align: center;
  max-width: calc(50% - 12px);
}
#modal-window {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: .8s ease;
  z-index: 10000000;
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
#modal-window-close {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#modal-window-inner {
  background-color: #0a0a0a;
  z-index: 3;
  width: 100%;
  position: relative;
  flex-grow: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-sizing: border-box;
}
#modal-window-inner-header {
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  font-size: 32px;
  font-weight: 900;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
}
#modal-window-inner-header-close {
  width: 36px;
  height: 36px;
  flex-shrink: 0;
}
#modal-window-inner-header-close svg {
  width: 100%;
  height: 100%;
}
#modal-form {
  flex-grow: 1;
  margin-top: 40px;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
}
#modal-form-inner {
  width: 100%;
  flex-grow: 1;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}
.modal-form-inner-col {
  width: calc(50% - 14px);
  display: flex;
  flex-direction: column;
}

@media (max-width: 960px) {
  #modal-form-inner {
    flex-direction: column;
  }
  .modal-form-inner-constructor-data-item {
    max-width: calc(50% - 12px);
    margin: 4px 8px 4px 0;
  }
  #modal-window {
    height: auto;
  }
  #modal-window-inner {
    padding: 20px;
  }
  #modal-form {
    flex-grow: 1;
    margin-top: 0;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
  }
  .modal-form-inner-col {
    width: auto;
    display: flex;
    flex-direction: column;
  }
  #modal-window-inner-header {
    font-size: 28px;
    margin-left: -5px;
    width: calc(100% + 10px);
    padding: 20px 5px;
    z-index: 12356467;
    background-color: #0a0a0a;
  }
  #modal-window-inner-header-close {
    width: 28px;
    height: 28px;
    flex-shrink: 0;
  }
  .modal-form-inner-feedback {
    margin-bottom: 28px;
  }
  #modal-form-submit-wrapper {
    justify-content: center;
  }
}

@media (max-width: 500px) {
  .modal-form-inner-constructor-data-item {
    max-width: calc(100% - 8px);
    margin: 4px 8px 4px 0;
    width: 100%;
  }
}
</style>