<script>
import './fonts.css';
import TabFont from "@/tabs/TabFont";
import TabText from "@/tabs/TabText";
import TabSize from "@/tabs/TabSize";
import TabWire from "@/tabs/TabWire";
import {backgroundsCarousel, tabIcons} from "@/images";
import {mapActions, mapGetters} from "vuex";
import TabColor from "@/tabs/TabColor";
import TabStand from "@/tabs/TabStand";
import TabBackgrounds from "@/tabs/TabBackgrounds";
import FormOrder from "./components/FormOrder";
import {formId, formWrapperId} from "@/ext";

export default {
  name: 'App',

  methods: {
    closeModal() {
      this.isModalOpen = false;
    },
    openModal() {
      if (this.text) {
        this.isModalOpen = true;
      }
    },
    hideTildaForm() {
      const tildaFormChecker = setInterval(() => {
        const tildaForm = document.getElementById( formWrapperId );

        if (tildaForm) {
          const tildaFormInputs = tildaForm.getElementsByClassName('t-form__inputsbox')[0];

          tildaForm.style.display = 'none';
          tildaFormInputs.remove();

          clearInterval(tildaFormChecker);
        }
      }, 100)
    },
  },

  created() {
    this.hideTildaForm();
  },

  computed: {
    ...mapGetters([
        'text',
        'font',
        'size',
        'isWireWhite',
        'stand',
        'color',
        'background',
        'backgrounds',
    ]),
    shadow() {
      const autoBlur = blur => `${blur}px`;
      return `
          2px 2px ${autoBlur(1)} rgba(0, 0, 0, 0.1),
          0 0 ${autoBlur(1)} ${this.color.glowColor},
          0 0 ${autoBlur(8)} ${this.color.glowColor},
          0 0 ${autoBlur(8)} ${this.color.glowColor},
          0 0 ${autoBlur(8)} ${this.color.glowColor},
          0 0 ${autoBlur(10)} ${this.color.glowColor},
          0 0 ${autoBlur(15)} ${this.color.glowColor},
          0 0 ${autoBlur(20)} ${this.color.glowColor},
          0 0 ${autoBlur(20)} ${this.color.glowColor},
          0 0 ${autoBlur(20)} ${this.color.glowColor},
          0 0 ${autoBlur(40)} ${this.color.glowColor},
          0 0 ${autoBlur(40)} ${this.color.glowColor},
          0 0 ${autoBlur(40)} ${this.color.glowColor}`;
    },
  },
  data: () => ({
    isModalOpen: false,
    tabs: [
      {component: 'TabText', title: 'Введите свой текст', icon: tabIcons.text,},
      {component: 'TabFont', title: 'Выберите шрифт', icon: tabIcons.font,},
      {component: 'TabColor', title: 'Выберите цвет неона', icon: tabIcons.color,},
      {component: 'TabBackgrounds', title: 'Попробуйте другой фон', icon: tabIcons.background,},
      {component: 'TabSize', title: 'Выберите высоту букв', icon: tabIcons.size,},
      {component: 'TabStand', title: 'Выберите тип подложки', icon: tabIcons.stand,},
      {component: 'TabWire', title: 'Выберите цвет провода', icon: tabIcons.wire,},
    ],
    isLightOn: true,
    activeTab: {component: 'TabText', title: 'Введите свой текст', icon: tabIcons.text,},
    activeBackground: 'backgroundPreview2',
  }),

  components: {
    TabText,
    TabFont,
    TabColor,
    FormOrder,
    TabSize,
    TabWire,
    TabBackgrounds,
    TabStand,
  },
}
</script>

<style>
