                Загрузи свой фон
              </label>
              <input type="file" accept="image/*" @change="dobriyVecher" name="photo" id="upload-photo"/>
            </div>
          </div>
        </div>
        <template #prevArrow="arrowOption">
          <div class="custom-arrow custom-arrow-left">
            <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="#ffffff" class="bi bi-chevron-left" viewBox="0 0 16 16"><path fill-rule="evenodd" d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/></svg>
          </div>
        </template>
        <template #nextArrow="arrowOption">
          <div class="custom-arrow custom-arrow-right">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="#ffffff" class="bi bi-chevron-right" viewBox="0 0 16 16"><path fill-rule="evenodd" d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/></svg>
          </div>
        </template>
      </VueSlickCarousel>
    </div>
  </div>
</template>

<script>
import VueSlickCarousel from "vue-slick-carousel";
import {mapActions, mapGetters} from "vuex";
import {backgrounds} from "@/ext";

export default {
  name: "TabBackgrounds",
  components: {
    VueSlickCarousel,
  },
  computed: {
    ...mapGetters([
      'background',
      'backgrounds',
    ]),
  },
  methods: {
    ...mapActions([
      'changeBackground',
      'pushBackground',
    ]),
    async dobriyVecher(e) {
      String.prototype.hashCode = function() {
        var hash = 0, i, chr;
        if (this.length === 0) return hash;
        for (i = 0; i < this.length; i++) {
          chr   = this.charCodeAt(i);
          hash  = ((hash << 5) - hash) + chr;
          hash |= 0;
        }
        return hash;
      };
      const toBase64 = file => new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
      });

      const baseImage = await toBase64(e.target.files[0]);
      const baseImageHash = Math.abs(baseImage.hashCode());
      this.pushBackground({
        id: baseImageHash,
        image: baseImage,
      });
      this.$refs.carousel.goTo(backgrounds.length - 1);
    },
    carouselChange(i) {
      this.changeBackground(backgrounds[i].id);
    },
  },
  data: () => ({
    settings: {
      arrows: true,
      centerPadding: '60px',
      focusOnSelect: true,
      centerMode: true,
      dots: false,
      initialSlide: 0,
      swipeToSlide: true,
      responsive: [
        {
          breakpoint: 960,
          settings: {
            centerPadding: '100px',
            slidesToShow:3,
          },
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow:1,
            centerPadding: '40px',
          },
        },
      ],
    },
  }),
}
</script>

<style scoped>
