  width: 100px;
  height: 100px;
}
.zhopa {
  width: 100px;
  background-color: rgba(255,255,255,0.05);
  font-size: 12px;
  text-align: center;
  display: flex;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: .4;
  height: 100px;
}
.tab-backgrounds-carousel-item-inner-custom {
  width: 100px;
  height: 100px;
}
.tab-backgrounds-carousel-item-inner-custom svg {
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
}
.custom-arrow {
  width: 25px;
  height: 25px;
}
.custom-arrow-left {
  left: -30px;
}
.custom-arrow-right {
  right: -30px;
}
.custom-arrow svg {
  width: 100%;
  height: 100%;
}
.custom-arrow::before {
  display: none;
}
@media (max-width: 960px) {
  .custom-arrow {
    width: 15px;
    height: 15px;
  }
  .custom-arrow-left {
    left: -20px;
  }
  .custom-arrow-right {
    right: -20px;
  }
  #tab-font-carousel {
    width: 100%;
    padding: 0 20px;
    align-self: center;
  }
}


</style>