<style scoped>
#tab-color {
  display: flex;
  align-items: stretch;
}
#tab-color-carousel {
  box-sizing: border-box;
  width: 100%;
}
.tab-color-item {
  display: flex;
  padding: 5px;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.tab-color-item-inner {
  background-color: rgba(255,255,255,0.11);
  width: 40px;
  height: 40px;
  display: flex;
  transition: .3s;
  align-items: center;
  cursor: pointer;
  justify-content: center;
}
.tab-color-item-inner__active {
  transform: scale(0.85, 0.85);
  background-color: white;
  color: black;
}
.tab-color-item .tab-color-item-inner-icon {
  display: none;
  width: 25px;
  height: 25px;
}
.tab-color-item .tab-color-item-inner-icon svg {
  width: 100%;
  height: 100%;
}
.tab-color-item-inner__active .tab-color-item-inner-icon {
  display: block;
}
.custom-arrow {
  width: 25px;
  height: 25px;
}
.custom-arrow-left {
  left: -30px;
}
.custom-arrow-right {
  right: -30px;
}
.custom-arrow svg {
  width: 100%;
  height: 100%;
}
.custom-arrow::before {
  display: none;
}
@media (max-width: 960px) {
  .custom-arrow {
    width: 15px;
    height: 15px;
  }
  .custom-arrow-left {
    left: -20px;
  }
  .custom-arrow-right {
    right: -20px;
  }
  #tab-color-carousel {
    width: 100%;
    padding: 0 20px;
    align-self: center;
  }
}

</style>